@use "global" as *;

/* 共通パーツ 下層ページ上のメインビュー*/
.mainview {
    position: relative;
}

.mainview__title {
    position: absolute;
    top: 82%;
    left: 6%;
    transform: translateX(-6%);
    font-weight: $medium;
    background-color: $blue;
    color: $white;
    padding: rem(12) rem(25) rem(10) rem(20);
    line-height: 1.2;

    @include mq(md) {
        font-size: rem(35);
        top: 87%;
        left: 13%;
        transform: translateX(-11.1%);
        padding: rem(20) rem(30) rem(15) rem(25);
        line-height: 1.1;
    }
}

.mainview__title--news-details {
    @include mq(md) {
        top: 78%;
    }
}

.mainview__title span {
    font-size: rem(30);
    text-transform: uppercase;
    font-family: $english-font-family;

    @include mq(md) {
        font-size: rem(40);
        letter-spacing: 0.02em;
    }
}

.mainview__title h1 {
    font-size: rem(16);
    font-weight: normal;

    @include mq(md) {
        font-size: rem(18);
        margin-top: 0;
    }
}

.mainview__sp {
    width: 100%;
    height: rem(225);

    @include mq(md) {
        display: none;
    }
}

.mainview__sp img {
    width: 100%;
    height: rem(225);
    object-fit: cover;
    object-position: 30% 60%;
}

.mainview__pc {
    display: none;


    @include mq(md) {
        display: block;
        width: 100%;
        height: rem(300);
    }
}

.mainview__pc img {
    @include mq(md) {
        height: rem(300);
        object-fit: cover;

    }
}